/**** Estilos compartidos en HTML de FORM-DESIGN + FORM-FILL + FORM-SHARE ****/
.item {
  width: 100%;
  display: flex;
  flex-direction: column;
  border-bottom: #d8d8d8 solid 1px;
  /**** Estilos de las CABECERAS de los desplegables  ****/
  /**** Estilos del CONTENIDO de los desplegables  ****/ }
  .item__header {
    font-family: "Open Sans", sans-serif;
    display: flex;
    justify-content: space-between;
    text-transform: uppercase;
    padding: 10px 10px 5px 10px; }
    .item__header:hover {
      cursor: pointer;
      background-color: #f5f5f5;
      color: #e17334; }
    .item__header--icon {
      color: #e17334;
      font-size: 18px;
      margin-right: 10px; }
    .item__header--title {
      display: flex;
      align-items: center; }
      .item__header--title h2 {
        font-family: "Open Sans", sans-serif;
        font-size: 18px;
        color: #54585a; }
    .item__header--arrowDown {
      font-size: 18px;
      color: #54585a; }
      .item__header--arrowDown:hover {
        cursor: pointer;
        color: #e17334;
        font-size: 20px; }
    .item__header--arrowUp {
      font-size: 18px;
      color: #e17334;
      transition: transform;
      transform: rotate(180deg); }
      .item__header--arrowUp:hover {
        cursor: pointer;
        color: #e17334;
        font-size: 20px; }
  .item__content {
    margin-bottom: 30px;
    margin-top: 20px; }
  .item .collapsed {
    display: none; }

/**** Estilos aplicados en sección DISEÑA ****/
